* {
  font-size: 18px;
}

body{
  display: flex;
  justify-content: center;
  align-items: center;
}

.container {
  top: 50%;
  left:50%;
  position: absolute;
  transform: translate(-50%,-50%);
  margin: 0;
  width: 256px;
  text-align: center;
  border:  5px solid #484a53;
  background:#484a53;
  border-radius: 5px;
}

.keypad{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(60px, auto);
  
}

input[type="text"]{
  height: 75px;
  width: 249px;
  background-color: #10111600;
  border: 0px;
  color: #ffffff;
  text-align: right;
  font-size: 25px;
  font-weight: 500;
  letter-spacing: 1px;
}

#clear {
  grid-column: 1/3;
  grid-row: 1;
  color: #262834;
}

#resultado{
  grid-column: 3/5;
  grid-row: 5;
  color: #262834;
}

button{
  margin: 2px;
}

.blue {
  background: #0e62cf;
  border: #0e62cf;
  color: #ffffff;
}

.blue-light{
  background: #498be0;;
  border: #498be0;
}